import { atom, selector } from 'recoil'

// export enum EStatus {
//   TODO = 'TODO',
//   DOING = 'DOING',
//   DONE = 'DONE',
// }

export interface ITodo {
  id: number
  text: string
  status: string
}

export const isDarkAtom = atom<boolean>({
  key: 'isDark',
  default: true,
})

export const currStatusListAtom = atom<string[]>({
  key: 'currStatusList',
  default: JSON.parse(localStorage.getItem('statusList') ?? '[]'),
})

export const currStatusAtom = atom<string>({
  key: 'currStatus',
  default: 'TODO',
})

export const todoStateAtom = atom<ITodo[]>({
  key: 'todo',
  default: JSON.parse(localStorage.getItem('todos') ?? '[]'),
})

/**
 * recoil selector  Vue store getter  .
 *  atom   
 *  useRecoilValue  selector    .
 */
export const todoSelector = selector({
  key: 'todoSelector',
  get({ get }) {
    const todos = get(todoStateAtom)
    const currStatus = get(currStatusAtom)
    return todos.filter((t) => t.status === currStatus)
  },
})
